/* 分頁導覽列 - 多頁式 */
.pagination {
  display: flex;
  justify-content: center; }
  .pagination li a {
    display: block;
    border: 1px solid #555555;
    color: #555555; }
    .pagination li a.active {
      background-color: #000; }
  .pagination li:first-child a, .pagination li:last-child a {
    font-size: 19px;
    line-height: 29px;
    padding: 11px 12px;
    background-color: #F2F2F2; }
    @media (max-width: 767px) {
      .pagination li:first-child a, .pagination li:last-child a {
        font-size: 14px;
        line-height: 26px;
        padding: 10px 10px; } }
  .pagination li a {
    font-size: 18px;
    line-height: 27px;
    padding: 12px 16px; }
    @media (max-width: 767px) {
      .pagination li a {
        font-size: 16px;
        line-height: 22px;
        padding: 12px 15px; } }

/* 分頁導覽列 - 左右分列前一頁下一頁導覽 */
.pagination-dual {
  display: flex;
  justify-content: space-between; }
  @media (max-width: 767px) {
    .pagination-dual {
      flex-direction: column;
      margin-bottom: 30px; } }
  .pagination-dual li {
    display: flex;
    align-items: center;
    font-size: 19px;
    line-height: 30px; }
    @media (max-width: 996px) {
      .pagination-dual li {
        font-size: 16px; } }
    @media (max-width: 767px) {
      .pagination-dual li {
        font-size: 14px; } }
    .pagination-dual li:hover {
      color: #aaa; }
    .pagination-dual li:first-child span {
      margin-right: 12px; }
      @media (max-width: 996px) {
        .pagination-dual li:first-child span {
          margin-right: 8px; } }
      @media (max-width: 767px) {
        .pagination-dual li:first-child span {
          margin-right: 4px; } }
    .pagination-dual li:last-child span {
